import React from 'react';
import MyServices from 'Content/Home/MyServices';
import MySkills from 'Content/Home/MySkills';
import C0ntentFullWidth from 'ContentFullWidth';

import './Home.css'
import homeData from 'data/home.json';

class Home extends React.Component {
  render() {
    return (
      <div id="content">
        <div className="row">
          <C0ntentFullWidth title="Hello" classNames="padding_30 padbot_30" >
            <p>
              Hi, my name is Mark, I’m a multi-disciplinary <span className="highlight">technologist.</span> I help companies build <span className="highlight">products</span> for happy <span className="highlight">customers.</span>
            </p>
            <p>
              When I’m not building products, you can find me being a father, riding motorbikes or woodworking.<br />
              Want to work together? I’d love to hear from you.
            </p>
          </C0ntentFullWidth>
        </div>

        <div className="row">
          <MyServices />
        </div>

        <div className="row">
          {homeData.skills.map((skill, index) => {
            return <MySkills key={index} title={skill.title} skills={skill.skillList} />
          })}
        </div>
      </div>
    );
  }
}

export default Home;



