import React from 'react';
import PropTypes from 'prop-types';
import Skill from 'Content/Home/MySkills/Skill';

class MySkills extends React.Component {

  static propTypes = {
    title: PropTypes.string.isRequired,
    skills: PropTypes.arrayOf(PropTypes.shape({
      title: PropTypes.string,
      percentage: PropTypes.number,
    }))
  }

  render() {
    return (
        <section className="design-skills col-md-6 padding_60 padbot_50">
            <div className="section-title bottom_45"><span></span><h2>{this.props.title}</h2></div>
            <ul className="skill-list">
              { this.props.skills.map(function(item, iterator) {
                return <Skill key={iterator} title={item.title} percentage={item.percentage} />
              })}
            </ul>
        </section>
    );
  }
}

export default MySkills;



