import React, { Component } from 'react';
import Showdown from 'showdown';
import CategoryList from './CategoryList';
import articleRepository from 'libs/ArticleRepository';
import TagList from './TagList';
import articleJson from 'data/articles.json';

import './Article.css'

const converter = new Showdown.Converter();

export default class Article extends Component {
  constructor(props) {
    super(props);
    const articleSlug = this.props.match.params.slug;  
    const article = articleJson.find(article => { return article.slug === articleSlug })

    this.state = { 
      title: article.title,
      body: { __html: converter.makeHtml(article.body) },
      categories: article.categories,
      tags: article.tags,
      categoriesWithCount: []
    };
  }

  componentDidMount() {
    window.Prism.highlightAll();
    articleRepository.categoriesWithCount((categoriesWithCount) => {
      this.setState({ categoriesWithCount: categoriesWithCount})
    })
  }

  componentDidUpdate() {
    window.Prism.highlightAll();
  }


  categoriesComponent() {   
    console.log(this.state.categoriesWithCount);
    
    if (Array.isArray(this.state.categoriesWithCount) && this.state.categoriesWithCount.length > 0) {  
      return (<CategoryList categories={this.state.categoriesWithCount} /> )
    }
  }

  tagsComponent() {
    if (this.state.tags && this.state.tags.length > 0) {
      return (<TagList tagList={this.state.tags} />)
    }
  }

  render() {
    return (
      <div id="content">

        <div id="article">
          <div className="row">
            <div className="col-md-8 post">
              <div className="post-content">
                <h1 className="blog-title bottom_30 top_30">{this.state.title}</h1>
                <div dangerouslySetInnerHTML={this.state.body} />
              </div>

              {this.tagsComponent()}
            </div>

            <div className="col-md-4 blog-sidebar">
              {this.categoriesComponent()}
            </div>
          </div>
        </div>
      </div>
    )
  }
}



